//购物车逻辑处理
const useCartEffect = ()=>{

    //数据库实例化
    const store = useStore()

    //商品存入购物车(调用)--外部js
    // const { cartList, changeCartItemInfo } = useCommonCartEffect();

    const { changeCartItemInfo } = useCommonCartEffect();

    const { cartList } = toRefs(store.state);
    
    const getProductCartCount =(cartList, shopId, productId)=>{

        console.log(cartList)
        return cartList?.[shopId]?.productList?.[productId]?.count || 0

    }

    //商品存入购物车(调用)--外部js
    //商铺ID, 商品ID, Tab导航, 数量, 店铺名称
    const changeCartItem =(shopId, productId, item, num, shopName)=>{

        //传入增减对应商品
        //店铺ID, 商品ID,  商品信息, 增减， 商铺名称
        changeCartItemInfo(shopId, productId, item, num)

        //同时把商店名称也带入
        //店铺ID, 商铺名称
        changeShopName(shopId, shopName)
    }

    //修改商铺名称的方法
    //商铺ID, 商铺名称
    const changeShopName =(shopId, shopName)=>{
        //派发一个commit方法
        store.commit('changeShopName',{shopId, shopName})
    }

    //数量显示简化
    

    return { cartList, changeCartItem, getProductCartCount }

}


export default {

    name:'Content',

    //获取父组件传递过来的内容
    props: [ 'shopName' ],

    setup(){
        //页面跳转实例化
        const route = useRoute()

        //获取商店页码
        const shopId = route.params.id

        //tab切换渲染(调用)
        const { currentTab, handleTabClick } = useTabEffect();

        //list内容(调用)
        //让list 依赖 currentTab ,随着currentTab的变化，list就发生变化
        const { list } = useCurrenListEffect(currentTab, shopId);

        //购物车逻辑处理(调用)
        const { cartList, changeCartItem, getProductCartCount } = useCartEffect();

        return { categories, currentTab, handleTabClick, list, shopId, cartList, changeCartItem, getProductCartCount }


        //商品存入购物车(调用)--外部js
