</script>
<style lang='scss' scoped>
@import '../../style/viriables.scss';
@import '../../style/mixins.scss';
    .content{
        display: flex;
        position: absolute;
        left:0;
        right:0;
        top:1.5rem;
        bottom: .5rem;
    }
    .category{
        overflow-y: scroll;
