<template>
    <div 
        class="mask" 
        v-if="showChart && calculations.total > 0"
        @click="handleCartShowChange"
    ></div>
    <div class="cart">
        <div 
            class="product"
            v-if="showChart && calculations.total > 0"
        >
            <div class="product__header">
                <div 
                    class="product__header__all"
                    @click="()=>setCartItemsChecked(shopId)"
                >
                    <span 
                        class="product__header__icon iconfont"
                        v-html="calculations.allChecked ? '&#xe652;' : '&#xe66c;' "
                    ></span>
                    全选
                </div>
                <div class="product__header__clear">
                    <span
                        class="product__header__clear__btn"
                        @click="()=>cleanCartProducts(shopId)"
                    >清空购物车
                    </span>
                </div>
            </div>
            <div 
                v-for=" item in productList"
                :key="item._id"
            >
                <div class="product__item" v-if="item.count > 0">
                    <div 
                        class="product__item__checked iconfont"
                        v-html="item.check ? '&#xe652;' : '&#xe66c;'"
                        @click="()=>changeCartItemChecked(shopId, item._id)"
                    >
                    </div>    
                    <img class="product__item__img" :src="item.imgUrl" />
                    <div class="product__item__detail">
                        <h4 class="product__item__title">{{item.name}}</h4>
                        <p class="product__item__price">
                            <span class="product__item__yen">&yen;</span>{{item.price}}
                            <span class="product__item__origin">&yen;{{item.oldPrice}}</span>
                        </p>
                    </div>
                    <div class="product__number">
                        <span 
                            class="product__number__minus"
                            @click="()=>{ changeCartItemInfo(shopId, item._id, item, -1) }"
                        >-</span>
                        <!-- //cartList?.[shopId]?.[item._id]?.count 尝试寻找 store里的数据 -->
                        <!-- cartList > shopId > productId > count  -->
                        <!-- {{cartList?.[shopId]?.[item._id]?.count || 0}} -->
                        <!-- 在Cart.vue里已经获取的情况下，简写上面内容 -->
                        {{item.count || 0}}

                        <!-- 点击加号向Cart里添加内容 -->
                        <span 
                            class="product__number__plus"
                            @click="()=>{ changeCartItemInfo(shopId, item._id, item, 1) }"
                        >+</span>
                    </div>
                </div>    
            </div>
        </div>
        <div class="check">
            <div class="check__icon">
                <img src="http://www.dell-lee.com/imgs/vue3/basket.png"
                     class="check__icon__img"
                     @click="handleCartShowChange"
                >
                <div class="check__icon__tag">{{calculations.total}}</div>
            </div>
            <div class="check__info">
                总计:<span class="check__info__price"> &yen;{{calculations.price}}</span>
            </div>
            <div class="check__btn" v-show="calculations.total > 0">
                <!-- 跳转页面时候带ID进去结算画面 -->
                <router-link :to="{path: `/oderConfirmation/${shopId}`}">
                    去结算
                </router-link>
            </div>
        </div>
    </div>
</template>
<script>
import { useStore } from 'vuex'
import { useRoute} from 'vue-router'
//计算属性
import { computed, ref } from 'vue'

//商品存入购物车(调用)--外部js
import { useCommonCartEffect } from './commonCartEffect'


//获取购物车信息逻辑
const useCartEffect=(shopId)=>{

    //数据库实例化
    const store = useStore();
    
    //商品存入购物车(调用)--外部js
    const { changeCartItemInfo } = useCommonCartEffect(shopId);

    //路由实例化
