    //获取store 里的 cartList的数据
    const cartList = store.state.cartList;

    // console.log(cartList);


    //计算属性整合(小货车里数量图标，金钱，图标选中)
    const calculations = computed(()=>{

        //获取第二层对应ID的情报
        const productList = cartList[shopId]?.productList

        //定义变量
        // let total = 0   //数量
        // let price = 0   //金钱
        // let allChecked = true //是否被选中

        //变量对象化
        const result = { total:0, price:0, allChecked:true}

        if(productList){
            for(let i in productList){

                //计算数量
                const product = productList[i]
                result.total += product.count

                //只计算被选中的商品
                if(product.check){
                    //计算它所有的钱
                    result.price += (product.count * product.price)
                }

                //判断它是非全选状态
                if(product.count > 0 && !product.check){
                    //计算它所有的钱
                    result.allChecked = false ;
                }
            }
        }
        // return { total, price, allChecked }
        //2位数转换
        result.price = result.price.toFixed(2);
        return result
    })

    //点击全选符号，选中所有商品
    const setCartItemsChecked = (shopId)=>{
        store.commit('setCartItemsChecked',{ shopId} )
    }

    //当前购物车商铺下，里面都有那些内容(获取购物车信息)
    const productList = computed(()=>{
            //获取第二层对应ID的情报
            const productList = cartList[shopId]?.productList || []

            return productList

    })

    //鼠标点击切换，购物车里的对号符号
    const changeCartItemChecked =(shopId, productId)=>{
        //修改数据库store的数据--(commit同步修改数据)
        store.commit('changeCartItemChecked',{
        //店铺ID，商品ID, 
            shopId, productId
        })
    }

    //购物车全部清空
    const cleanCartProducts=(shopId)=>{
        //数据修改
        store.commit('cleanCartProducts',{ shopId })
    }

    return { productList, changeCartItemInfo, changeCartItemChecked, cleanCartProducts, calculations, setCartItemsChecked}


    // //计算属性(小货车里的图标)
