    //     if(productList){
    //         //循环productList
    //         for(let i in productList ){
    //             //获取当前的product(寻找每一个prodcut) 
    //             const product = productList[i]
    //             //判断它是否没选中
    //             if(product.check){
    //                 //计算它所有的钱
    //                 count += (product.count * product.price)
    //             }
    //         }
    //     }
    //     //返回2位数小数点
    //     return count.toFixed(2); 
    // })

    // //计算属性(是否所有商品都被选中)
    // const allChecked = computed(()=>{

    //     //获取第二层对应ID的情报
    //     const productList = cartList[shopId]?.productList

    //     //初期全选
    //     let result = true

    //     //如果第二层有数据
    //     if(productList){
    //         //循环productList
    //         for(let i in productList ){
    //             //获取当前的product(寻找每一个prodcut) 
    //             const product = productList[i]
    //             //判断它是否没选中
    //             if(product.count > 0 && !product.check){
    //                 //计算它所有的钱
    //                 result = false ;
    //             }
    //         }
    //     }
    //     //返回2位数小数点
    //     return result; 
    // })


    
}

 //展示隐藏购物车逻辑
const toggleCarrEffect =()=>{
    //显示购物车初始值-不显示
    const showChart =ref(false)

    //事件点击，显示购物车内容
    const handleCartShowChange =()=>{
        showChart.value = !showChart.value  
    }
        
    return { showChart,  handleCartShowChange  }
}

export default {
    name: 'Cart',
    setup(){
        //路由实例化
        const route = useRoute()

        //获取当前店铺ID
        const shopId = route.params.id
        
        //获取购物车信息逻辑
        const {  productList, changeCartItemInfo, changeCartItemChecked, cleanCartProducts, calculations, setCartItemsChecked, } = useCartEffect(shopId)

        //展示隐藏购物车逻辑
        const { showChart,  handleCartShowChange } =toggleCarrEffect()

        return { productList, shopId, changeCartItemInfo, changeCartItemChecked, cleanCartProducts, calculations, setCartItemsChecked, showChart, handleCartShowChange}


