<style lang="scss" scoped>
@import '../../style/mixins.scss';
@import '../../style/viriables.scss';
    .mask{
       position:fixed;
       left:0;
       right:0;
       bottom:0;
       top:0;
       background:rgba(0, 0, 0, .5);
       z-index:1;
    }
    .cart{
        position:absolute;
        left:0;
        right:0;
        bottom:0;
        z-index:2;
        background: $bgColor;
    }
    .check{
        display: flex;
        box-sizing: border-box;
        height: .49rem;
        border-top: .01rem solid $content-bgColor;
        line-height: .49rem;
        &__icon{
            position: relative;
            width: .84rem;
            &__img{
                display: block;
                margin: .12rem auto;
                width: .28rem;
                height: .26rem;
            }
            &__tag{
                left:.46rem;
                top:.04rem;
                position:absolute;
                padding:0 .03rem;
                min-width: .2rem;
                height: .2rem;
                line-height: .2rem;
                text-align: center;
                background-color:$hightlight-fontColor;
                border-radius: .1rem;
                font-size: .12rem;
                color:$bgColor;
                transform: scale(.5);
                transform-origin: left center;
            }
        }
        &__info{
            flex:1;
            color:$content-fontcolor;
            font-size: .1rem;
            &__price{
                color:$hightlight-fontColor;
                font-size: .18rem;
            }
        }
        &__btn{
            width: .98rem;
            background:#4fb0f9;
            font-size: .14rem;
            text-align: center;
            a{
                color:$bgColor;
                text-decoration: none;
            }
        }
    }
.product{
        overflow-y:scroll;
        flex:1;
        background: $bgColor;
        &__header{
            display: flex;
            line-height: .52rem;
            border-bottom:.01rem solid $content-bgColor;
            font-size: .14rem;
            color: $content-fontcolor;
            &__all{
                width: .64rem;
                margin-left: .18rem;
            }
            &__icon{
                display: inline-block;
                vertical-align:top;
                margin-right: .1rem;
                color:$btn-bgColor;
                font-size: .21rem;
            }
            &__clear{ 
                flex: 1;
                margin-right: .16rem;
                text-align:right;
                &__btn{
                    display: inline-block;
                }
            }
        }
        &__item{
            position: relative;
            display: flex;
            padding: .12rem 0;
            margin: 0 .16rem;
            border-bottom: .01rem solid $content-bgColor;
            &__checked{
                color: $btn-bgColor;
                margin-right: .2rem;
                font-size: .2rem;
                line-height: .5rem;
            }
            &__detail{
                overflow: hidden;
            }
            &__img{
                width: .46rem;
                height: .46rem;
                margin-right: .16rem;
            }
            &__title{
                margin:0;
                line-height: .2rem;
                font-size: .14rem;
                color: $content-fontcolor;
                @include ellipsis;
            }
            &__price{
                margin: .06rem 0 0 0;
                line-height: .2rem;
                font-size: .14rem;
                color: $hightlight-fontColor;
            }
            &__yen{
                font-size: .12rem;
            }
            &__origin{
                margin-left:.06rem;
                line-height: .2rem;
                font-size: .12rem;
                color:$light-fontColor;
                text-decoration: line-through;
            }
            .product__number{
                position: absolute;
                right: 0;
                bottom: .26rem;
                &__minus,
                &__plus{
                    display: inline-block;
                    width: .2rem;
                    height: .2rem;
                    line-height: .16rem;
                    border-radius: 50%;
                    font-size: .2rem;
                    text-align: center;
                }
                &__minus{
                    position: relative;
                    top: .02rem;
                    border:.01rem solid $medium-fontColor;
                    color:$medium-fontColor;
                    margin-right: .05rem;
                }
                &__plus{
                    position: relative;
                    top: .02rem;
                    background:$btn-bgColor;
                    color:$bgColor;
                    margin-left: .05rem;
                }
            }
        }
}
</style>