<template>
    <div class="wrapper">
        <div class="Ssearch">
            <div 
                class="Ssearch__back iconfont"
                @click="handleBackClick"
            >&#xe61f;</div>
            <div class="Ssearch__content">
                <span class="Ssearch__content__icon iconfont">&#xe600;</span>
                <input 
                    class="Ssearch__content__input" 
                    placeholder="请输入商品名称"
                />
            </div>
        </div>
        <!-- 解决在图片没展示出来之前的，空白效果
        v-if="item.imgUrl -->
        <ShopInfo  :item="item" :hideBorder="true" v-if="item.imgUrl"/>
        <Content :shopName="item.name"/>
        <Cart />
    </div>
</template>

<script>
//useRouter是获取一些大的信息
//useRoute 你当前路径的一些信息
import { useRouter, useRoute } from 'vue-router'
//useRouter 获取
import { reactive, toRefs } from 'vue'
import { get } from '../../utils/request'
import ShopInfo from '../../components/ShopInfo'
import Content from './Content'
import Cart from './Cart'


//获取当前商铺信息
const useShopInfoEffect =()=>{
    //useRoute 你当前路径的一些信息
    const route = useRoute()
    //创建一个reactive的数据对象
    //从数据库获取数据，并一次性替换，且精简，必须让item是reatvie里的一个对象
    const data = reactive({ item: {} })
    
    const getItemData = async () =>{
        //请求
        const result = await get(`/api/shop/${route.params.id}`)
         if(result?.errno === 0 && result?.data){
            data.item =  result.data
        }
    }
    //消除前缀
    const { item } = toRefs(data)
    return { item, getItemData }
}


const useBackRouterEffect = ()=>{
    const router = useRouter()
    const handleBackClick =()=>{
            router.back()
    }
    return { handleBackClick }
}




export default {
    name:'Shop',
    components: { ShopInfo, Content, Cart },
    setup(){
        

        //获取封装方法
        const { item, getItemData } = useShopInfoEffect();

        //获取返回上一页的方法
        const { handleBackClick } = useBackRouterEffect();

        getItemData();
        return{ item, handleBackClick}

        
        //手写的数据
        // const item = {
        //     id:"1",
        //     name:"沃尔玛2",
        //     imgUrl:"http://www.dell-lee.com/imgs/vue3/near.png",
        //     sales:10000,
        //     expressLimit:0,
        //     expressPrice:5,
        //     slogan:"VIP尊享满89元运费卷"
        // }

        //创建一个reactive的数据对象
        //从数据库获取数据，并一次性替换，且精简，必须让item是reatvie里的一个对象
        // const data = reactive({ item: {} })
        // //服务器获取数据
        // const getItemData = async () =>{
        //     //请求
        //     const result = await get(`/api/shop/${route.params.id}`)
        //     if(result?.errno === 0 && result?.data){
        //         data.item =  result.data
        //     }
        // }
        
    }
}
</script>

<style lang="scss" scoped>
@import '../../style/viriables.scss';
.wrapper{
    padding: 0 .18rem;
    .Ssearch{
        line-height: .32rem;
        display: flex;
        margin: .14rem 0 .04rem 0;
        &__back{
            width: .3rem;
            font-size: .24rem;
            color:#b6b6b6;
        }
        &__content{
            display:flex;
            flex: 1;
            background: $search-bgColor;
            border-radius: .16rem;
            border-radius: .16rem;
            &__icon{
                width: .44rem;
                text-align:center;
                line-height: .32rem;
                font-size: .24rem;
                color: $search-fontColor;
            }
            &__input{
                display: block;
                width:100%;
                padding-right:.02rem;
                border: none;
                outline:none;
                background:none;
                height: .32rem;
                color: $content-fontcolor;
                &::placeholder{
                    color: $content-fontcolor;
                    font-size:.14rem;
                }
            }
        }
    }
}
</style>