<template>
    <div class="wrapper">
        <img  class="wrapper__img" src="http://www.dell-lee.com/imgs/vue3/user.png">
        <div class="wrapper__input">
            <input 
                class="wrapper__input__content"  
                placeholder="请输入用户名"
                v-model="username"
            >
        </div>
        <div class="wrapper__input">
            <input 
                class="wrapper__input__content"  
                placeholder="请输入密码"
                type="password"
                v-model="password"
            >
        </div>
        <div class="wrapper__login-button" @click="handleLogin">登录</div>
        <div class="wrapper__login-link" @click="handlerRegisterClick">立即注册</div>
        <!-- 展示Toast -->
        <Toast v-if="show" :message="toastMessage"/>
    </div>
</template>

<script>
//引用路由 useRouter获取路由的实例
import { reactive, toRefs } from 'vue';
import { useRouter } from 'vue-router';
import { post } from '../../utils/request';

//引用提示画面
// import Toast from '../../components/Toast'; 只调入toast.vue
import Toast, { userToastEffect } from '../../components/Toast';

//login封装-传递showToast
const useLoginEffect =(showToast)=>{
        //获取路由的实例
        const router = useRouter();
        //定义数据
        const data = reactive({ username:'', password:'' })
        //登录过程(封装)
        const handleLogin = async()=>{
            try{
                //获取输入的内容
                const { username, password} = data;
                //判断输入内容是否为空
                if( username == '' && password == ''){
                    showToast('输入内容不能为空');
                    return
                }
                //axios的请求
                const result = await post('/api/user/login',{
                    username: data.username,
                    password: data.password,
                })
                //判断获取的数据
                if(result?.errno === 0){
                    localStorage.isLogin = true
                    router.push({name:'Home'})
                }else{
                    // data.showToast = true    
                    // data.toastMessage = '登录失败'
                    // setTimeout(()=>{
                    //     data.showToast = false
                    //     data.toastMessage = ''
                    // },2000)
                    // alert('登录失败');
                        
                    //函数调用
                    showToast('登录失败')
                }
            }catch(e){
                //函数调用
                showToast('请求失败')
            }
        }

    const { username , password} = toRefs(data)
    return { username, password, handleLogin}
}
//页面跳转到注册页封装
const useRegisterEffect = ()=>{
    //获取路由的实例
    const router = useRouter();

    //注册页面
    const handlerRegisterClick =()=>{
        //跳转到注册页面
        router.push({name:'Register'})
    }
    return { handlerRegisterClick }

}

//控制弹窗的内容(只限此页面，多页面调用需要到toast.vue里写入)
// const userToastEffect =()=>{
//     const toastData = reactive({
//         showToast: false,
//         toastMessage:'',
//     })
//     const showToast =(message)=>{
//         toastData.showToast = true    
//         toastData.toastMessage = message
//         setTimeout(()=>{
//             toastData.showToast = false
//             toastData.toastMessage = ''
//         },2000)
//     }
//     return { toastData, showToast}
// }

//封装--utils
//接入数据的类型修改成-json
// axios.defaults.headers.post['Content-Type'] = 'application/json';
//引入数据请求工具
// import axios from 'axios';
///


export default {
    name:'Login',
    //引用components-Toast
    components:{ Toast },
    // 职责就是告诉你，代码执行的一个流程
    setup(){
        //获取路由的实例
        
        //定义一个登录数据(封装userToastEffect)
        // const data = reactive({
        //     username: '',
        //     password: '',
        //     // showToast: false,
        //     // toastMessage:'',
        // })

        //引入外部异文件(弹出画面处理)的封装函数
        const { showToast, toastMessage, show} = userToastEffect()

        //引入外部同文件(登录页面的处理)的封装
        const { username, password, handleLogin} = useLoginEffect(showToast)

        //引入外部同文件(页面跳转到注册)的封装
        const { handlerRegisterClick } = useRegisterEffect()


        //登录(普通)
        // const handleLogin = ()=>{
        //     //1.后端判断数据，需要先安装axios请求工具
        //     axios.post('https://www.fastmock.site/mock/723e201a1d0222d0d2a870ad346b3691/lichengyuan/api/user/login',{
        //         username: data.username,
        //         password: data.password
        //     }).then(()=>{
        //         //浏览器存储数据
        //         localStorage.isLogin = true;
        //         //跳转到主页
        //         router.push({name:'Home'})
        //     }).catch(()=>{
        //         alert('失败');
        //     })
        // }

        // //注册页面
        // const handlerRegisterClick =()=>{
        //     //跳转到注册页面
        //     router.push({name:'Register'})
        // }

        return{ 
            password, username,
            show, toastMessage,
            handleLogin, handlerRegisterClick,
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../../style/viriables.scss';
    .wrapper{
        position:absolute;
        top:50%;
        left:0;
        right:0;
        transform: translateY(-50%);
        &__img{
            display: block;
            margin: 0 auto .4rem auto;
            width: .66rem;
            height: .66rem;
        }
        &__input{
            box-sizing: border-box;
            height: .48rem;
            margin: 0  .4rem .16rem .4rem;
            padding: 0 .16rem;
            background: #f9f9f9;
            border: .01rem solid rgba(0,0,0,.10);
            border-radius: 0.06rem;
            border-radius: 0.06rem;
            &__content{
                margin-top: .12rem;
                line-height: .22rem;
                border:none;
                outline:none;
                width: 100%;
                background:none;
                font-size: .16rem;
                color: $content-notice-fontcolor;
                &::placeholder {
                    color: $content-notice-fontcolor;
                }
            }
        }
        &__login-button{
            margin: 0.32rem .4rem 0.16rem .4rem;
            line-height: .48rem;
            background: $btn-bgColor;
            box-shadow: 0 .04rem .08rem 0 rgba(0,145,255,.32);
            border-radius: .04rem;
            border-radius: .04rem;
            color: $bgColor;
            font-size: .16rem;
            text-align: center;
        }
        &__login-link{
            text-align: center;
            font-size: .14rem;
            color: $content-notice-fontcolor;
        }
    }
</style>